import React, { useState, useEffect } from 'react';
import { Container, Row, Col, Card, Badge, Form, InputGroup, Button, Alert, Spinner } from 'react-bootstrap';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';

const VendorListPage = () => {
  const [vendors, setVendors] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedCategory, setSelectedCategory] = useState('');
  const [minRating, setMinRating] = useState('');
  const [menuSearchTerm, setMenuSearchTerm] = useState('');
  const [searchTimeout, setSearchTimeout] = useState(null);
  
  const navigate = useNavigate();
  
  // Kategori seenekleri
  const categoryOptions = [
    'Trk Mutfa',
    'Dnya Mutfa',
    'Fast Food',
    'Deniz rnleri',
    'Vejetaryen',
    'Vegan',
    'Tatllar',
    'ecekler',
    'Kahvalt',
    'le Yemei',
    'Akam Yemei',
    'Sokak Lezzetleri',
    'Pasta ve Brek',
    'Dier'
  ];
  
  useEffect(() => {
    // Debounce ilemi - her tu vuruundan sonra deil, kk bir beklemeden sonra arama yapar
    const fetchVendors = async () => {
      try {
        setLoading(true);
        
        // Filtreleri olutur
        let url = 'http://localhost:5000/api/vendors';
        let params = [];
        
        if (searchTerm) {
          params.push(`name=${searchTerm}`);
        }
        
        if (selectedCategory) {
          params.push(`category=${selectedCategory}`);
        }
        
        if (minRating) {
          params.push(`rating=${minRating}`);
        }
        
        if (menuSearchTerm) {
          params.push(`menuItem=${menuSearchTerm}`);
        }
        
        if (params.length > 0) {
          url += `?${params.join('&')}`;
        }
        
        const { data } = await axios.get(url);
        
        if (data.success) {
          // Eer men aramas yaplyorsa, sadece eleen men eleri olan restoranlar gster
          if (menuSearchTerm && menuSearchTerm.trim() !== '') {
            const filteredVendors = data.data.filter(
              vendor => vendor.matchedMenuItems && vendor.matchedMenuItems.length > 0
            );
            setVendors(filteredVendors);
          } else {
            setVendors(data.data);
          }
        }
      } catch (error) {
        setError(
          error.response && error.response.data.message
            ? error.response.data.message
            : 'Restoranlar yklenirken bir hata olutu'
        );
      } finally {
        setLoading(false);
      }
    };
    
    // Debounce mekanizmas: klavye vuruu 400ms durunca arama yap
    if (searchTimeout) {
      clearTimeout(searchTimeout);
    }
    
    setSearchTimeout(
      setTimeout(() => {
        fetchVendors();
      }, 400)
    );
    
    // Component unmount olduunda timeout'u temizle
    return () => {
      if (searchTimeout) {
        clearTimeout(searchTimeout);
      }
    };
    
  }, [searchTerm, selectedCategory, minRating, menuSearchTerm]);
  
  const handleSearch = (e) => {
    e.preventDefault();
    // Form gnderimi - Sayfa yenilenmesini engeller
  };
  
  const handleMenuSearch = (e) => {
    // Yazldka dorudan arama yapacak ekilde deeri gncelle
    setMenuSearchTerm(e.target.value);
  };
  
  const viewVendorDetails = (vendorId) => {
    navigate(`/vendor/${vendorId}`);
  };

  return (
    <Container className="py-4">
      <h1 className="mb-4">Restoranlar</h1>
      
      {error && <Alert variant="danger">{error}</Alert>}
      
      <Card className="mb-4 shadow-sm">
        <Card.Body>
          <Form onSubmit={handleSearch}>
            <Row className="mb-3">
              <Col md={5}>
                <Form.Group className="mb-3 mb-md-0">
                  <Form.Label>Restoran Ad</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="text"
                      placeholder="Restoran ara..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                    />
                    <Button type="submit" variant="primary">
                      Ara
                    </Button>
                  </InputGroup>
                </Form.Group>
              </Col>
              <Col md={4}>
                <Form.Group className="mb-3 mb-md-0">
                  <Form.Label>Kategori</Form.Label>
                  <Form.Select
                    value={selectedCategory}
                    onChange={(e) => setSelectedCategory(e.target.value)}
                  >
                    <option value="">Tm Kategoriler</option>
                    {categoryOptions.map((category) => (
                      <option key={category} value={category}>
                        {category}
                      </option>
                    ))}
                  </Form.Select>
                </Form.Group>
              </Col>
              <Col md={3}>
                <Form.Group>
                  <Form.Label>Minimum Puan</Form.Label>
                  <Form.Select
                    value={minRating}
                    onChange={(e) => setMinRating(e.target.value)}
                  >
                    <option value="">Tm Puanlar</option>
                    <option value="4">4 ve zeri </option>
                    <option value="3">3 ve zeri </option>
                    <option value="2">2 ve zeri </option>
                    <option value="1">1 ve zeri </option>
                  </Form.Select>
                </Form.Group>
              </Col>
            </Row>
            
            <Row>
              <Col md={12}>
                <Form.Group>
                  <Form.Label>Men erii Ara</Form.Label>
                  <InputGroup>
                    <Form.Control
                      type="text"
                      placeholder="Yemek, tatl, iecek vb. ara..."
                      value={menuSearchTerm}
                      onChange={handleMenuSearch}
                    />
                    {menuSearchTerm && (
                      <Button 
                        variant="outline-secondary" 
                        onClick={() => setMenuSearchTerm('')}
                      >
                        <i className="bi bi-x-lg"></i>
                      </Button>
                    )}
                    <Button variant="secondary" disabled={loading}>
                      {loading ? <Spinner animation="border" size="sm" /> : <i className="bi bi-search"></i>}
                    </Button>
                  </InputGroup>
                  <Form.Text className="text-muted">
                    Men ieriine gre arama yapmak iin yazmaya balayn. rn: kebap, pizza, stl tatl vb.
                  </Form.Text>
                </Form.Group>
              </Col>
            </Row>
          </Form>
        </Card.Body>
      </Card>
      
      {loading ? (
        <div className="text-center py-4">
          <Spinner animation="border" variant="primary" />
          <p className="mt-2">Restoranlar ykleniyor...</p>
        </div>
      ) : vendors.length === 0 ? (
        <Alert variant="info">
          {menuSearchTerm ? (
            <>Aramanzla eleen men esi bulunamad. Baka bir terim deneyin.</>
          ) : (
            <>Arama kriterlerinize uygun restoran bulunamad.</>
          )}
        </Alert>
      ) : (
        <Row>
          {vendors.map((vendor) => (
            <Col key={vendor._id} md={6} lg={4} className="mb-4">
              <Card className="h-100 shadow-sm border-0 vendor-card" as={Link} to={`/vendors/${vendor._id}`} style={{ textDecoration: 'none', color: 'inherit' }}>
                <Card.Img 
                  variant="top" 
                  src={vendor.coverImage !== 'no-cover.jpg' ? vendor.coverImage : 'https://via.placeholder.com/300x150?text=Restoran'} 
                  alt={vendor.businessName}
                  style={{ height: '150px', objectFit: 'cover' }}
                />
                <Card.Body>
                  <Card.Title className="mb-1">{vendor.businessName}</Card.Title>
                  <div className="mb-2">
                    {[...Array(5)].map((_, index) => (
                      <i 
                        key={index}
                        className={`bi ${index < Math.round(vendor.rating) ? 'bi-star-fill' : 'bi-star'}`}
                        style={{ color: '#ffc107' }}
                      ></i>
                    ))}
                    <span className="ms-1 text-muted">({vendor.totalReviews || 0} deerlendirme)</span>
                  </div>
                  
                  <div className="mb-2">
                    {vendor.categories.slice(0, 3).map((category, index) => (
                      <Badge key={index} bg="light" text="dark" className="me-1 border">
                        {category}
                      </Badge>
                    ))}
                    {vendor.categories.length > 3 && (
                      <Badge bg="light" text="dark">
                        +{vendor.categories.length - 3}
                      </Badge>
                    )}
                  </div>
                  
                  <div className="mb-2 text-muted small">
                    <i className="bi bi-geo-alt-fill me-1"></i>
                    {vendor.address.city}, {vendor.address.state}
                  </div>
                  
                  {/* Eleen men elerini gster - minimal tasarm */}
                  {vendor.matchedMenuItems && vendor.matchedMenuItems.length > 0 && (
                    <div className="mt-3">
                      <div className="d-flex align-items-center">
                        <i className="bi bi-check-circle-fill text-success me-1"></i>
                        <span className="text-success fw-bold">Aradnz Men eleri ({vendor.matchedMenuItems.length})</span>
                      </div>
                      
                      <div className="menu-items-list mt-2">
                        {vendor.matchedMenuItems.map(item => (
                          <div 
                            key={item._id} 
                            className="d-flex justify-content-between align-items-center py-2 px-2 mt-1 menu-item"
                            style={{ 
                              backgroundColor: '#f8f9fa', 
                              borderRadius: '4px'
                            }}
                          >
                            <div className="d-flex align-items-center">
                              {menuSearchTerm ? (
                                <span dangerouslySetInnerHTML={{ 
                                  __html: item.name.replace(
                                    new RegExp(`(${menuSearchTerm})`, 'gi'), 
                                    '<strong style="color: #28a745;">$1</strong>'
                                  ) 
                                }} />
                              ) : (
                                item.name
                              )}
                              {item.isVegetarian && (
                                <span className="ms-1 text-success small">
                                  <i className="bi bi-flower1"></i>
                                </span>
                              )}
                            </div>
                            <div className="fw-bold">{item.price.toFixed(2)} </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  )}
                </Card.Body>
              </Card>
            </Col>
          ))}
        </Row>
      )}
    </Container>
  );
};

export default VendorListPage; 