import React, { useState, useEffect, useCallback } from 'react';
import { Container, Row, Col, Card, Tab, Nav, Table, Badge, Button, Alert, Modal, Form, Spinner, Image, ListGroup } from 'react-bootstrap';
import { useParams, useNavigate, Link } from 'react-router-dom';
import axios from 'axios';

const VendorDetailPage = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  
  const [vendor, setVendor] = useState(null);
  const [menuItems, setMenuItems] = useState([]);
  const [availability, setAvailability] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [tab, setTab] = useState('menu');
  const [selectedItem, setSelectedItem] = useState(null);
  const [showCartModal, setShowCartModal] = useState(false);
  const [quantity, setQuantity] = useState(1);
  const [specialInstructions, setSpecialInstructions] = useState('');
  const [addingToCart, setAddingToCart] = useState(false);
  const [cartError, setCartError] = useState('');
  const [cartSuccess, setCartSuccess] = useState({ show: false, item: null });
  
  // Rezervasyon modal durumlar
  const [showReservationModal, setShowReservationModal] = useState(false);
  const [selectedDay, setSelectedDay] = useState(null);
  const [availableSlots, setAvailableSlots] = useState([]);
  const [selectedSlot, setSelectedSlot] = useState('');
  const [reservationLoading, setReservationLoading] = useState(false);
  const [reservationSuccess, setReservationSuccess] = useState(false);
  const [reservationError, setReservationError] = useState('');
  const [orderNote, setOrderNote] = useState('');
  
  // debug amal token kontrol
  useEffect(() => {
    const token = localStorage.getItem('token');
    console.log('Mevcut token:', token ? token.substring(0, 20) + '...' : 'Yok');
  }, []);
  
  // Bugnn tarihini alp gn ismini bulmak iin
  const getDayName = (date) => {
    const days = ['Pazar', 'Pazartesi', 'Sal', 'aramba', 'Perembe', 'Cuma', 'Cumartesi'];
    return days[date.getDay()];
  };
  
  const today = new Date();
  const currentDay = getDayName(today);
  
  // Tarihi formatlama fonksiyonu
  const formatDate = (date) => {
    return `${date.getDate().toString().padStart(2, '0')}.${(date.getMonth() + 1).toString().padStart(2, '0')}.${date.getFullYear()}`;
  };
  
  // Saat dilimlerini hesapla
  const calculateTimeSlots = useCallback((dayInfo) => {
    if (!dayInfo || !dayInfo.isOpen) return [];
    
    const slots = [];
    const [openHour, openMinute] = dayInfo.openTime.split(':').map(Number);
    const [closeHour, closeMinute] = dayInfo.closeTime.split(':').map(Number);
    
    let currentHour = openHour;
    let currentMinute = openMinute;
    
    // alma saatleri iindeki tm zaman dilimlerini hesapla
    while (
      currentHour < closeHour || 
      (currentHour === closeHour && currentMinute < closeMinute)
    ) {
      const timeString = `${currentHour.toString().padStart(2, '0')}:${currentMinute.toString().padStart(2, '0')}`;
      slots.push(timeString);
      
      // Sonraki zaman dilimini hesapla
      currentMinute += dayInfo.slotDurationMinutes;
      if (currentMinute >= 60) {
        currentHour += Math.floor(currentMinute / 60);
        currentMinute = currentMinute % 60;
      }
    }
    
    return slots;
  }, []);
  
  useEffect(() => {
    const fetchVendorDetails = async () => {
      try {
        // Satc detaylarn getir
        const { data: vendorData } = await axios.get(`http://localhost:5000/api/vendors/${id}`);
        
        if (vendorData.success) {
          setVendor(vendorData.data);
          
          // Satcnn mensn getir
          const { data: menuData } = await axios.get(`http://localhost:5000/api/vendors/${id}/menu`);
          
          if (menuData.success) {
            setMenuItems(menuData.data);
          }
          
          // Msaitlik bilgilerini getir
          const userToken = localStorage.getItem('token');
          if (userToken) {
            try {
              const config = {
                headers: {
                  Authorization: `Bearer ${userToken}`
                }
              };
              
              const { data: availabilityData } = await axios.get(
                `http://localhost:5000/api/vendors/${id}/availability`,
                config
              );
              
              if (availabilityData.success) {
                // Gnleri doru srada dizme
                const dayOrder = ['Pazartesi', 'Sal', 'aramba', 'Perembe', 'Cuma', 'Cumartesi', 'Pazar'];
                const sortedAvailability = [...availabilityData.data].sort((a, b) => {
                  return dayOrder.indexOf(a.day) - dayOrder.indexOf(b.day);
                });
                
                setAvailability(sortedAvailability);
              }
            } catch (error) {
              console.log('Msaitlik bilgileri alnamad, devam ediliyor...');
            }
          }
        }
      } catch (error) {
        setError(
          error.response && error.response.data.message
            ? error.response.data.message
            : 'Restoran bilgileri yklenirken bir hata olutu'
        );
      } finally {
        setLoading(false);
      }
    };
    
    fetchVendorDetails();
  }, [id]);
  
  const openReservationModal = (day) => {
    const selectedDayInfo = availability.find(a => a.day === day);
    
    if (selectedDayInfo && selectedDayInfo.isOpen) {
      setSelectedDay(selectedDayInfo);
      const slots = calculateTimeSlots(selectedDayInfo);
      setAvailableSlots(slots);
      setSelectedSlot('');
      setOrderNote('');
      setReservationError('');
      setReservationSuccess(false);
      setShowReservationModal(true);
    } else {
      alert(`${day} gn iin rezervasyon alnmamaktadr.`);
    }
  };
  
  const makeReservation = async () => {
    if (!selectedSlot) {
      setReservationError('Ltfen bir zaman dilimi sein');
      return;
    }
    
    const userToken = localStorage.getItem('token');
    if (!userToken) {
      navigate('/login', { state: { from: `/vendor/${id}` } });
      return;
    }
    
    try {
      setReservationLoading(true);
      setReservationError('');
      
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${userToken}`
        }
      };
      
      // Gnn tarihini hesapla
      const today = new Date();
      const dayIndex = ['Pazar', 'Pazartesi', 'Sal', 'aramba', 'Perembe', 'Cuma', 'Cumartesi'].indexOf(selectedDay.day);
      const dayDiff = dayIndex - today.getDay();
      const reservationDate = new Date(today);
      reservationDate.setDate(today.getDate() + (dayDiff >= 0 ? dayDiff : dayDiff + 7));
      
      const { data } = await axios.post(
        `http://localhost:5000/api/reservations`,
        {
          vendor: id,
          date: reservationDate.toISOString().split('T')[0],
          time: selectedSlot,
          note: orderNote
        },
        config
      );
      
      if (data.success) {
        setReservationSuccess(true);
        
        // 3 saniye sonra modal kapat
        setTimeout(() => {
          setShowReservationModal(false);
        }, 3000);
      }
    } catch (error) {
      setReservationError(
        error.response && error.response.data.message
          ? error.response.data.message
          : 'Rezervasyon oluturulurken bir hata olutu'
      );
    } finally {
      setReservationLoading(false);
    }
  };

  // Kategori gruplarna gre men elerini grupla
  const getMenuByCategories = () => {
    const categories = {};
    
    menuItems.forEach(item => {
      if (!categories[item.category]) {
        categories[item.category] = [];
      }
      categories[item.category].push(item);
    });
    
    return categories;
  };

  const closeAddToCartModal = () => {
    setShowCartModal(false);
    setSelectedItem(null);
    setQuantity(1);
    setSpecialInstructions('');
    setCartError('');
    setCartSuccess({ show: false, item: null });
  };

  // Sepete direkt olarak ekleme (miktar kontrol olmadan)
  const quickAddToCart = (item) => {
    try {
      setAddingToCart(true);
      
      const token = localStorage.getItem('token');
      const userInfo = localStorage.getItem('userInfo');
      
      // Kullanc giri yapmadysa login sayfasna ynlendir
      if (!token || !userInfo) {
        // eyi localStorage'a kaydet
        const cartItem = {
          menuItemId: item._id,
          menuItem: item,
          quantity: 1,
          specialInstructions: ''
        };
        
        localStorage.setItem('pendingCartItem', JSON.stringify(cartItem));
        navigate('/login');
        return;
      }
      
      // Kullanc giri yapmsa API istei gnder
      const postData = async () => {
        try {
          const config = {
            headers: {
              'Content-Type': 'application/json',
              'Authorization': `Bearer ${token}`
            }
          };
          
          const response = await axios.post(
            'http://localhost:5000/api/cart',
            {
              menuItemId: item._id,
              quantity: 1,
              notes: ''
            },
            config
          );
          
          if (response.data.success) {
            alert(`${item.name} sepete eklendi!`);
            window.dispatchEvent(new Event('storage'));
            document.dispatchEvent(new CustomEvent('cartUpdated'));
          }
        } catch (error) {
          console.error('API hatas:', error);
          alert('rn sepete eklenirken bir hata olutu!');
        } finally {
          setAddingToCart(false);
        }
      };
      
      postData();
    } catch (error) {
      console.error('Hzl sepet ekleme hatas:', error);
      alert('rn eklenirken bir hata olutu!');
      setAddingToCart(false);
    }
  };

  const handleAddToCart = async () => {
    // Quantity validation
    if (quantity < 1) {
      setCartError('Ltfen geerli bir miktar girin');
      return;
    }

    setAddingToCart(true);
    setCartError('');
    
    try {
      const token = localStorage.getItem('token');
      const userInfo = localStorage.getItem('userInfo');
      
      // Kullanc giri yapmadysa direkt olarak geici sepete ekleyelim
      if (!token || !userInfo) {
        // rn benzersiz ID'si olutur
        const tempItemId = `temp_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
        
        // Geici sepet esini olutur
        const cartItem = {
          _id: tempItemId,
          menuItemId: selectedItem._id,
          menuItem: {
            _id: selectedItem._id,
            name: selectedItem.name,
            price: selectedItem.price,
            description: selectedItem.description,
            image: selectedItem.image
          },
          quantity: quantity,
          notes: specialInstructions,
          vendorId: id
        };
        
        // pendingCartItem olarak kaydet ve login sayfasna ynlendir
        localStorage.setItem('pendingCartItem', JSON.stringify(cartItem));
        navigate('/login');
        return;
      }
      
      // Kullanc giri yapmsa normal API ars yap
      console.log('Sepete ekleme istei gnderiliyor:', {
        menuItemId: selectedItem._id,
        quantity: quantity,
        notes: specialInstructions
      });

      const config = {
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        }
      };

      const response = await axios.post(
        'http://localhost:5000/api/cart',
        {
          menuItemId: selectedItem._id,
          quantity: quantity,
          notes: specialInstructions
        },
        config
      );

      console.log('Sepete ekleme yant:', response.data);
      
      // API'den baarl yant geldiinde
      setCartSuccess({ show: true, item: selectedItem });
      
      // Storage event'i tetikle ki header sepet saysn gncellesin
      window.dispatchEvent(new Event('storage'));
      
      // zel bir event de tetikleyelim
      document.dispatchEvent(new CustomEvent('cartUpdated'));
      
      // Kullancya baarl mesajn gster ve sepet sayfasna ynlendir
      setTimeout(() => {
        closeAddToCartModal();
        navigate('/cart');
      }, 1500);
      
    } catch (error) {
      console.error('Sepete ekleme hatas:', error);
      setCartError(error.response?.data?.error || error.response?.data?.message || 'Sepete eklenirken bir hata olutu');
    } finally {
      setAddingToCart(false);
    }
  };

  const openAddToCartModal = (item) => {
    setSelectedItem(item);
    setShowCartModal(true);
  };
  
  if (loading) {
    return (
      <Container className="py-5 text-center">
        <Spinner animation="border" variant="primary" />
        <p className="mt-2">Restoran bilgileri ykleniyor...</p>
      </Container>
    );
  }

  if (error) {
    return (
      <Container className="py-5">
        <Alert variant="danger">
          {error}
        </Alert>
        <Button variant="primary" onClick={() => navigate('/vendors')}>
          Restoranlar Sayfasna Dn
        </Button>
      </Container>
    );
  }

  if (!vendor) {
    return (
      <Container className="py-5">
        <Alert variant="warning">
          Restoran bulunamad
        </Alert>
        <Button variant="primary" onClick={() => navigate('/vendors')}>
          Restoranlar Sayfasna Dn
        </Button>
      </Container>
    );
  }

  const menuByCategories = getMenuByCategories();

  return (
    <Container className="py-4">
      <Card className="mb-4 shadow-sm">
        <Card.Body>
          <Button
            variant="outline-secondary"
            className="mb-3"
            onClick={() => navigate('/vendors')}
          >
             Restoranlar
          </Button>
          
          <Row className="align-items-center">
            <Col md={3} className="text-center text-md-start">
              <img 
                src={vendor.logo !== 'no-photo.jpg' ? vendor.logo : 'https://via.placeholder.com/150?text=Logo'} 
                alt={vendor.businessName}
                className="img-fluid rounded"
                style={{ maxWidth: '150px' }}
              />
            </Col>
            <Col md={9}>
              <h1>{vendor.businessName}</h1>
              <div className="mb-2">
                {[...Array(5)].map((_, index) => (
                  <i 
                    key={index}
                    className={`bi ${index < Math.round(vendor.rating) ? 'bi-star-fill' : 'bi-star'}`}
                    style={{ color: '#ffc107' }}
                  ></i>
                ))}
                <span className="ms-1">({vendor.totalReviews} deerlendirme)</span>
              </div>
              <div className="mb-2">
                {vendor.categories.map((category, index) => (
                  <Badge key={index} bg="secondary" className="me-1">
                    {category}
                  </Badge>
                ))}
              </div>
              <p className="text-muted">
                <i className="bi bi-geo-alt-fill me-1"></i>
                {vendor.address.street}, {vendor.address.city}, {vendor.address.state}
              </p>
              <p>
                <i className="bi bi-telephone-fill me-1"></i>
                {vendor.phoneNumber}
              </p>
            </Col>
          </Row>
        </Card.Body>
      </Card>
      
      <Tab.Container id="vendor-details-tabs" defaultActiveKey="menu">
        <Card className="shadow-sm">
          <Card.Header>
            <Nav variant="tabs">
              <Nav.Item>
                <Nav.Link eventKey="menu">Men</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="availability">Msaitlik</Nav.Link>
              </Nav.Item>
              <Nav.Item>
                <Nav.Link eventKey="info">Bilgiler</Nav.Link>
              </Nav.Item>
            </Nav>
          </Card.Header>
          <Card.Body>
            <Tab.Content>
              <Tab.Pane eventKey="menu">
                {menuItems.length === 0 ? (
                  <Alert variant="info">
                    Bu restoran iin men bilgisi bulunamad.
                  </Alert>
                ) : (
                  <>
                    {Object.keys(menuByCategories).map(category => (
                      <div key={category} className="mb-4">
                        <h4 className="mb-3">{category}</h4>
                        <Row>
                          {menuByCategories[category].map(item => (
                            <Col key={item._id} md={6} className="mb-3">
                              <Card className="h-100">
                                <Row className="g-0">
                                  <Col xs={4}>
                                    <img
                                      src={item.image !== 'default-food.jpg' ? item.image : 'https://via.placeholder.com/150?text=Yemek'}
                                      alt={item.name}
                                      className="img-fluid rounded-start"
                                      style={{ height: '100%', objectFit: 'cover' }}
                                    />
                                  </Col>
                                  <Col xs={8}>
                                    <Card.Body>
                                      <div className="d-flex justify-content-between align-items-start">
                                        <Card.Title>{item.name}</Card.Title>
                                        <h5 className="text-primary">{item.price.toFixed(2)} </h5>
                                      </div>
                                      <Card.Text className="mb-2" style={{ fontSize: '0.9rem' }}>
                                        {item.description.length > 80
                                          ? `${item.description.substring(0, 80)}...`
                                          : item.description}
                                      </Card.Text>
                                      <div className="d-flex justify-content-between">
                                        <div>
                                          {item.isVegetarian && (
                                            <Badge bg="success" className="me-1">Vejetaryen</Badge>
                                          )}
                                          {item.isVegan && (
                                            <Badge bg="success" className="me-1">Vegan</Badge>
                                          )}
                                          {item.isGlutenFree && (
                                            <Badge bg="warning" text="dark" className="me-1">Glutensiz</Badge>
                                          )}
                                          {item.spicyLevel > 0 && (
                                            <Badge bg="danger">
                                              {[...Array(item.spicyLevel)].map((_, i) => (
                                                <i key={i} className="bi bi-fire"></i>
                                              ))}
                                            </Badge>
                                          )}
                                        </div>
                                        <Button 
                                          variant="outline-primary" 
                                          size="sm"
                                          onClick={() => quickAddToCart(item)}
                                        >
                                          <i className="bi bi-cart-plus"></i> Sepete Ekle
                                        </Button>
                                      </div>
                                    </Card.Body>
                                  </Col>
                                </Row>
                              </Card>
                            </Col>
                          ))}
                        </Row>
                      </div>
                    ))}
                  </>
                )}
              </Tab.Pane>
              <Tab.Pane eventKey="availability">
                <h3 className="mb-3">alma Saatleri ve Randevu</h3>
                <p className="mb-4">
                  Aadaki tabloda restorann alma saatlerini grebilir ve randevu alabilirsiniz.
                  Bugn <strong>{currentDay}</strong> ({formatDate(today)})
                </p>
                
                {availability.length === 0 ? (
                  <Alert variant="info">
                    Bu restoran iin msaitlik bilgisi bulunamad.
                  </Alert>
                ) : (
                  <Table bordered responsive>
                    <thead className="bg-light">
                      <tr>
                        <th>Gn</th>
                        <th>Durum</th>
                        <th>alma Saatleri</th>
                        <th>Aksiyon</th>
                      </tr>
                    </thead>
                    <tbody>
                      {availability.map((day, index) => (
                        <tr 
                          key={index} 
                          className={day.day === currentDay ? 'table-primary' : ''}
                        >
                          <td>{day.day}</td>
                          <td>
                            {day.isOpen ? (
                              <Badge bg="success">Ak</Badge>
                            ) : (
                              <Badge bg="danger">Kapal</Badge>
                            )}
                          </td>
                          <td>
                            {day.isOpen ? (
                              `${day.openTime} - ${day.closeTime}`
                            ) : (
                              'Kapal'
                            )}
                          </td>
                          <td>
                            <Button
                              variant="outline-primary"
                              size="sm"
                              disabled={!day.isOpen}
                              onClick={() => openReservationModal(day.day)}
                            >
                              Randevu Al
                            </Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                )}
              </Tab.Pane>
              <Tab.Pane eventKey="info">
                <Row>
                  <Col md={8}>
                    <h4 className="mb-3">Restoran Hakknda</h4>
                    <p>{vendor.description}</p>
                    
                    <h5 className="mt-4 mb-3">letiim Bilgileri</h5>
                    <p><strong>Adres:</strong> {vendor.address.street}, {vendor.address.city}, {vendor.address.state}, {vendor.address.zipCode}</p>
                    <p><strong>Telefon:</strong> {vendor.phoneNumber}</p>
                    {vendor.email && <p><strong>E-posta:</strong> {vendor.email}</p>}
                    {vendor.website && <p><strong>Web Sitesi:</strong> <a href={vendor.website} target="_blank" rel="noreferrer">{vendor.website}</a></p>}
                    
                    {vendor.socialMedia && (
                      <div className="mt-3">
                        {vendor.socialMedia.facebook && (
                          <a href={vendor.socialMedia.facebook} target="_blank" rel="noreferrer" className="me-2">
                            <i className="bi bi-facebook fs-4"></i>
                          </a>
                        )}
                        {vendor.socialMedia.instagram && (
                          <a href={vendor.socialMedia.instagram} target="_blank" rel="noreferrer" className="me-2">
                            <i className="bi bi-instagram fs-4"></i>
                          </a>
                        )}
                        {vendor.socialMedia.twitter && (
                          <a href={vendor.socialMedia.twitter} target="_blank" rel="noreferrer" className="me-2">
                            <i className="bi bi-twitter fs-4"></i>
                          </a>
                        )}
                      </div>
                    )}
                  </Col>
                  <Col md={4}>
                    <Card className="shadow-sm">
                      <Card.Body>
                        <h5 className="mb-3">Bilgiler</h5>
                        <p><strong>Mutfak:</strong> {vendor.cuisine}</p>
                        <p><strong>Minimum Sipari:</strong> {vendor.minOrderAmount > 0 ? `${vendor.minOrderAmount} ` : 'Yok'}</p>
                        <p><strong>Teslimat creti:</strong> {vendor.deliveryFee > 0 ? `${vendor.deliveryFee} ` : 'cretsiz'}</p>
                        <p><strong>Tahmini Teslimat:</strong> {vendor.estimatedDeliveryTime} dakika</p>
                      </Card.Body>
                    </Card>
                  </Col>
                </Row>
              </Tab.Pane>
            </Tab.Content>
          </Card.Body>
        </Card>
      </Tab.Container>
      
      {/* Rezervasyon Modal */}
      <Modal show={showReservationModal} onHide={() => setShowReservationModal(false)}>
        <Modal.Header closeButton>
          <Modal.Title>Randevu Olutur</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {reservationSuccess ? (
            <Alert variant="success">
              Randevunuz baaryla oluturuldu! Teekkr ederiz.
            </Alert>
          ) : (
            <>
              {selectedDay && (
                <Form>
                  <Form.Group className="mb-3">
                    <Form.Label>Randevu Gn</Form.Label>
                    <Form.Control 
                      type="text" 
                      value={selectedDay.day} 
                      readOnly 
                    />
                  </Form.Group>
                  
                  <Form.Group className="mb-3">
                    <Form.Label>Saat</Form.Label>
                    <Form.Select
                      value={selectedSlot}
                      onChange={(e) => setSelectedSlot(e.target.value)}
                      required
                    >
                      <option value="">Saat Sein</option>
                      {availableSlots.map((slot) => (
                        <option key={slot} value={slot}>
                          {slot}
                        </option>
                      ))}
                    </Form.Select>
                  </Form.Group>
                  
                  <Form.Group className="mb-3">
                    <Form.Label>Not (stee Bal)</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      value={orderNote}
                      onChange={(e) => setOrderNote(e.target.value)}
                      placeholder="zel isteklerinizi buraya yazabilirsiniz"
                    />
                  </Form.Group>
                  
                  {reservationError && (
                    <Alert variant="danger">{reservationError}</Alert>
                  )}
                </Form>
              )}
            </>
          )}
        </Modal.Body>
        <Modal.Footer>
          {!reservationSuccess && (
            <>
              <Button variant="secondary" onClick={() => setShowReservationModal(false)}>
                ptal
              </Button>
              <Button 
                variant="primary" 
                onClick={makeReservation}
                disabled={reservationLoading}
              >
                {reservationLoading ? 'leniyor...' : 'Randevu Olutur'}
              </Button>
            </>
          )}
        </Modal.Footer>
      </Modal>
      
      {/* Sepete rn Ekleme Modal */}
      <Modal show={showCartModal} onHide={closeAddToCartModal} centered>
        <Modal.Header closeButton>
          <Modal.Title>Sepete Ekle</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          {selectedItem && (
            <div className="d-flex flex-column">
              <div className="d-flex mb-3">
                <div className="me-3">
                  <img 
                    src={selectedItem.image || "https://via.placeholder.com/80"} 
                    alt={selectedItem.name} 
                    style={{ width: "80px", height: "80px", objectFit: "cover" }} 
                    className="rounded"
                  />
                </div>
                <div>
                  <h5>{selectedItem.name}</h5>
                  <p className="text-muted mb-1">{selectedItem.description}</p>
                  <p className="fw-bold">{selectedItem.price.toFixed(2)}</p>
                </div>
              </div>
              
              {cartSuccess.show ? (
                <Alert variant="success" className="text-center">
                  <i className="bi bi-check-circle-fill me-2"></i>
                  <strong>{cartSuccess.item?.name}</strong> sepete eklendi!
                </Alert>
              ) : (
                <>
                  {cartError && <Alert variant="danger">{cartError}</Alert>}
                  
                  <Form.Group className="mb-3">
                    <Form.Label>Miktar</Form.Label>
                    <div className="d-flex align-items-center">
                      <Button 
                        variant="outline-secondary" 
                        onClick={() => setQuantity(Math.max(1, quantity - 1))}
                        disabled={quantity <= 1}
                      >
                        <i className="bi bi-dash"></i>
                      </Button>
                      <Form.Control
                        type="number"
                        min="1"
                        value={quantity}
                        onChange={(e) => setQuantity(parseInt(e.target.value) || 1)}
                        className="mx-2 text-center"
                      />
                      <Button 
                        variant="outline-secondary" 
                        onClick={() => setQuantity(quantity + 1)}
                      >
                        <i className="bi bi-plus"></i>
                      </Button>
                    </div>
                  </Form.Group>
                  
                  <Form.Group className="mb-3">
                    <Form.Label>zel stekler (stee Bal)</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={2}
                      value={specialInstructions}
                      onChange={(e) => setSpecialInstructions(e.target.value)}
                      placeholder="Ekstra malzeme, piirme tercihi vb."
                    />
                  </Form.Group>
                </>
              )}
            </div>
          )}
        </Modal.Body>
        <Modal.Footer>
          {!cartSuccess.show && (
            <>
              <Button variant="outline-secondary" onClick={closeAddToCartModal}>
                ptal
              </Button>
              <Button 
                variant="primary" 
                onClick={handleAddToCart}
                disabled={addingToCart}
              >
                {addingToCart ? (
                  <>
                    <Spinner
                      as="span"
                      animation="border"
                      size="sm"
                      role="status"
                      aria-hidden="true"
                      className="me-2"
                    />
                    Ekleniyor...
                  </>
                ) : (
                  'Sepete Ekle'
                )}
              </Button>
            </>
          )}
        </Modal.Footer>
      </Modal>
    </Container>
  );
};

export default VendorDetailPage; 