import React, { useState, useEffect } from 'react';
import { Form, Button, Container, Row, Col, Card, Alert, Image } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const VendorProfileCreate = () => {
  const [formData, setFormData] = useState({
    businessName: '',
    description: '',
    address: {
      street: '',
      city: '',
      state: '',
      zipCode: '',
      country: 'Trkiye'
    },
    phoneNumber: '',
    categories: ['Trk Mutfa'],
    cuisine: 'Trk Mutfa'
  });
  
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(false);
  const [userInfo, setUserInfo] = useState(null);
  const [logoFile, setLogoFile] = useState(null);
  const [coverFile, setCoverFile] = useState(null);
  const [logoPreview, setLogoPreview] = useState(null);
  const [coverPreview, setCoverPreview] = useState(null);
  const [uploadingImages, setUploadingImages] = useState(false);

  const navigate = useNavigate();
  
  // Kategoriler listesi
  const categoryOptions = [
    'Trk Mutfa',
    'Dnya Mutfa',
    'Fast Food',
    'Deniz rnleri',
    'Vejetaryen',
    'Vegan',
    'Tatllar',
    'ecekler',
    'Kahvalt',
    'le Yemei',
    'Akam Yemei',
    'Sokak Lezzetleri',
    'Pasta ve Brek',
    'Dier'
  ];

  useEffect(() => {
    // localStorage'dan kullanc bilgilerini al
    const storedUserInfo = localStorage.getItem('userInfo');
    if (!storedUserInfo) {
      navigate('/login');
      return;
    }
    
    const userInfo = JSON.parse(storedUserInfo);
    setUserInfo(userInfo);
    
    // Satc profilinin olup olmadn kontrol et
    const checkVendorProfile = async () => {
      try {
        const token = localStorage.getItem('userToken');
        const config = {
          headers: {
            Authorization: `Bearer ${token}`
          }
        };
        
        const { data } = await axios.get('http://localhost:5000/api/vendors/profile', config);
        
        // Eer profil varsa, dzenleme sayfasna ynlendir
        if (data.success) {
          navigate('/vendor/profile/edit');
        }
      } catch (error) {
        // Profil bulunamad, bu sayfada kalabilir
        if (error.response && error.response.status !== 404) {
          setError('Bir hata olutu: ' + error.response.data.message);
        }
      }
    };
    
    checkVendorProfile();
  }, [navigate]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    
    if (name.includes('.')) {
      const [parent, child] = name.split('.');
      setFormData({
        ...formData,
        [parent]: {
          ...formData[parent],
          [child]: value
        }
      });
    } else {
      setFormData({
        ...formData,
        [name]: value
      });
    }
  };
  
  const handleCategoryChange = (e) => {
    const { value, checked } = e.target;
    
    if (checked) {
      setFormData({
        ...formData,
        categories: [...formData.categories, value]
      });
    } else {
      setFormData({
        ...formData,
        categories: formData.categories.filter(cat => cat !== value)
      });
    }
  };
  
  const handleLogoChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setLogoFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setLogoPreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };
  
  const handleCoverChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setCoverFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setCoverPreview(reader.result);
      };
      reader.readAsDataURL(file);
    }
  };
  
  // Resim ykleme fonksiyonu
  const uploadImage = async (file, type) => {
    const formData = new FormData();
    formData.append('image', file);
    formData.append('type', type); // 'logo' veya 'cover'
    
    const token = localStorage.getItem('userToken');
    const config = {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: `Bearer ${token}`
      }
    };
    
    const { data } = await axios.post(
      'http://localhost:5000/api/vendors/upload',
      formData,
      config
    );
    
    return data.imageUrl;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Basit dorulama
    if (!formData.businessName || !formData.description || !formData.phoneNumber) {
      setError('Ltfen tm gerekli alanlar doldurun');
      return;
    }
    
    if (formData.categories.length === 0) {
      setError('En az bir kategori semelisiniz');
      return;
    }
    
    if (!formData.address.street || !formData.address.city || !formData.address.state || !formData.address.zipCode) {
      setError('Ltfen tm adres alanlarn doldurun');
      return;
    }
    
    try {
      setLoading(true);
      setError('');
      
      // nce resimleri ykle
      let vendorData = { ...formData };
      
      if (logoFile || coverFile) {
        setUploadingImages(true);
        try {
          // Logo ykleme
          if (logoFile) {
            const logoUrl = await uploadImage(logoFile, 'logo');
            vendorData.logo = logoUrl;
          }
          
          // Kapak resmi ykleme
          if (coverFile) {
            const coverUrl = await uploadImage(coverFile, 'cover');
            vendorData.coverImage = coverUrl;
          }
        } catch (error) {
          console.error('Resim ykleme hatas:', error);
          setError('Resimler yklenirken bir hata olutu');
          setLoading(false);
          setUploadingImages(false);
          return;
        }
        setUploadingImages(false);
      }
      
      const token = localStorage.getItem('userToken');
      const config = {
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${token}`
        }
      };
      
      const { data } = await axios.post(
        'http://localhost:5000/api/vendors/profile',
        vendorData,
        config
      );
      
      if (data.success) {
        setSuccess(true);
        
        // localStorage'daki kullanc bilgilerini gncelle
        const userInfo = JSON.parse(localStorage.getItem('userInfo'));
        if (userInfo) {
          userInfo.vendorProfile = data.data;
          localStorage.setItem('userInfo', JSON.stringify(userInfo));
          window.dispatchEvent(new Event('storage'));
        }
        
        // 2 saniye sonra satc paneline ynlendir
        setTimeout(() => {
          navigate('/vendor/dashboard');
        }, 2000);
      }
    } catch (error) {
      setError(
        error.response && error.response.data.message
          ? error.response.data.message
          : 'Profil oluturulurken bir hata olutu'
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container className="py-4">
      <Row className="justify-content-md-center">
        <Col lg={8}>
          <Card className="shadow-sm">
            <Card.Body className="p-4">
              <h2 className="text-center mb-4">letme Profili Olutur</h2>
              
              {error && <Alert variant="danger">{error}</Alert>}
              {success && <Alert variant="success">Profil baaryla oluturuldu! Ynlendiriliyorsunuz...</Alert>}
              
              <Form onSubmit={handleSubmit}>
                <h5 className="mt-4 mb-3">Grsel Bilgileri</h5>
                
                <Row className="mb-4">
                  <Col md={6} className="mb-3 mb-md-0">
                    <Form.Group controlId="logoFile">
                      <Form.Label>letme Logosu</Form.Label>
                      <Form.Control
                        type="file"
                        accept="image/*"
                        onChange={handleLogoChange}
                      />
                      <Form.Text className="text-muted">
                        nerilen boyut: 300x300 piksel. Maks 1MB.
                      </Form.Text>
                      {logoPreview && (
                        <div className="mt-2 text-center">
                          <Image 
                            src={logoPreview} 
                            alt="Logo nizleme" 
                            style={{ maxWidth: '200px', maxHeight: '200px' }} 
                            thumbnail 
                          />
                        </div>
                      )}
                    </Form.Group>
                  </Col>
                  
                  <Col md={6}>
                    <Form.Group controlId="coverFile">
                      <Form.Label>Kapak Resmi</Form.Label>
                      <Form.Control
                        type="file"
                        accept="image/*"
                        onChange={handleCoverChange}
                      />
                      <Form.Text className="text-muted">
                        nerilen boyut: 1200x300 piksel. Maks 2MB.
                      </Form.Text>
                      {coverPreview && (
                        <div className="mt-2 text-center">
                          <Image 
                            src={coverPreview} 
                            alt="Kapak Resmi nizleme" 
                            style={{ maxWidth: '100%', maxHeight: '150px' }} 
                            thumbnail 
                          />
                        </div>
                      )}
                    </Form.Group>
                  </Col>
                </Row>
                
                <h5 className="mt-4 mb-3">letme Bilgileri</h5>
                
                <Form.Group className="mb-3" controlId="businessName">
                  <Form.Label>letme Ad*</Form.Label>
                  <Form.Control
                    type="text"
                    name="businessName"
                    value={formData.businessName}
                    onChange={handleChange}
                    placeholder="letme adnz girin"
                    required
                  />
                </Form.Group>
                
                <Form.Group className="mb-3" controlId="description">
                  <Form.Label>letme Aklamas*</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={3}
                    name="description"
                    value={formData.description}
                    onChange={handleChange}
                    placeholder="letmeniz hakknda ksa bir aklama yazn"
                    required
                  />
                </Form.Group>
                
                <Form.Group className="mb-3" controlId="phoneNumber">
                  <Form.Label>Telefon Numaras*</Form.Label>
                  <Form.Control
                    type="text"
                    name="phoneNumber"
                    value={formData.phoneNumber}
                    onChange={handleChange}
                    placeholder="Telefon numaranz girin (r. 0555 123 4567)"
                    required
                  />
                </Form.Group>
                
                <h5 className="mt-4 mb-3">Adres Bilgileri</h5>
                
                <Row>
                  <Col md={12} className="mb-3">
                    <Form.Group controlId="address.street">
                      <Form.Label>Sokak/Cadde*</Form.Label>
                      <Form.Control
                        type="text"
                        name="address.street"
                        value={formData.address.street}
                        onChange={handleChange}
                        placeholder="Sokak/Cadde ad, bina no, daire no"
                        required
                      />
                    </Form.Group>
                  </Col>
                  
                  <Col md={6} className="mb-3">
                    <Form.Group controlId="address.city">
                      <Form.Label>ehir*</Form.Label>
                      <Form.Control
                        type="text"
                        name="address.city"
                        value={formData.address.city}
                        onChange={handleChange}
                        placeholder="ehir"
                        required
                      />
                    </Form.Group>
                  </Col>
                  
                  <Col md={6} className="mb-3">
                    <Form.Group controlId="address.state">
                      <Form.Label>le*</Form.Label>
                      <Form.Control
                        type="text"
                        name="address.state"
                        value={formData.address.state}
                        onChange={handleChange}
                        placeholder="le"
                        required
                      />
                    </Form.Group>
                  </Col>
                  
                  <Col md={6} className="mb-3">
                    <Form.Group controlId="address.zipCode">
                      <Form.Label>Posta Kodu*</Form.Label>
                      <Form.Control
                        type="text"
                        name="address.zipCode"
                        value={formData.address.zipCode}
                        onChange={handleChange}
                        placeholder="Posta kodu"
                        required
                      />
                    </Form.Group>
                  </Col>
                  
                  <Col md={6} className="mb-3">
                    <Form.Group controlId="address.country">
                      <Form.Label>lke</Form.Label>
                      <Form.Control
                        type="text"
                        name="address.country"
                        value={formData.address.country}
                        onChange={handleChange}
                        disabled
                      />
                    </Form.Group>
                  </Col>
                </Row>
                
                <h5 className="mt-4 mb-3">Mutfak Bilgileri</h5>
                
                <Form.Group className="mb-3" controlId="cuisine">
                  <Form.Label>Ana Mutfak Tr*</Form.Label>
                  <Form.Select
                    name="cuisine"
                    value={formData.cuisine}
                    onChange={handleChange}
                    required
                  >
                    {categoryOptions.map((category, index) => (
                      <option key={index} value={category}>
                        {category}
                      </option>
                    ))}
                  </Form.Select>
                </Form.Group>
                
                <Form.Group className="mb-4" controlId="categories">
                  <Form.Label>Kategoriler (En az bir kategori sein)*</Form.Label>
                  <div className="d-flex flex-wrap">
                    {categoryOptions.map((category, index) => (
                      <Form.Check
                        key={index}
                        type="checkbox"
                        id={`category-${index}`}
                        label={category}
                        value={category}
                        checked={formData.categories.includes(category)}
                        onChange={handleCategoryChange}
                        className="me-3 mb-2"
                      />
                    ))}
                  </div>
                </Form.Group>
                
                <div className="d-grid gap-2 mt-4">
                  <Button 
                    variant="primary" 
                    type="submit"
                    disabled={loading || success || uploadingImages}
                  >
                    {loading ? 'Kaydediliyor...' : uploadingImages ? 'Resimler Ykleniyor...' : 'Profil Olutur'}
                  </Button>
                  
                  <Button 
                    variant="outline-secondary" 
                    onClick={() => navigate('/vendor/dashboard')}
                    disabled={loading || success || uploadingImages}
                  >
                    ptal
                  </Button>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
};

export default VendorProfileCreate; 